
.App{z-index: 10;}
.App-header {
    height: max-content;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    color: white;
}
.overflowHidden{
    overflow: hidden;
}
.clippedBody{
    position: absolute;
    height: 40vh;
    width: 100vw; 
    clip-path: polygon(0 100%,100% 100%,100% 0,0 7vw);   
}
#clippedBody1{
    background-color: #7c7a78;    
    z-index: -20;
    top: 15vh;
}
#clippedBody2{
    background-image: url("../images/bckgrnd1_blurred.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    z-index: -15;
    top: 35vh;
}
#clippedBody3{
    background-color: #292726; 
    z-index: -10;
    height: 40vh;
    top: 55vh;
}
.clippedDistancer{
    position: absolute;
    height: 40vh;
    width: 100vw; 
    background-color: #fffaff;
    clip-path: polygon(0 100%,100% 100%,100% 0,0 7vw);
}
#clippedDistancer1{
    z-index: -17;
    top: 34.5vh;
}
#clippedDistancer2{
    z-index: -13;
    top: 54.5vh;
}
#clippedDistancer3{
    z-index: -8;
    top: 74.5vh;
}

#shape1{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 2px dotted #fffaff;
    background-color: #3e92cc;
    position: absolute;
    left: 10%;
    top: 30%;
    z-index: -1;
}

#shape2{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #3e92cc;
    background-color: #0a2463;
    position: absolute;
    left: 90%;
    top: 70%;
    z-index: -1;
}

